import React, { Component } from 'react'
import styles from "../Swiper/assets/css/swiper.module.css";
import banner1 from './assets/images/banner1.jpg';
import banner2 from './assets/images/banner2.jpg';
import banner3 from './assets/images/banner3.jpg';
import banner4 from './assets/images/banner4.jpg';
import banner5 from './assets/images/banner5.jpg';
import gg from "./assets/images/gg.png";
export default class Swiper extends Component {
    constructor(){
        super();
        this.state = {
            data:[
                {id:1,cont:"减肥or吃"},
                {id:2,cont:"运动or休息"},
                {id:3,cont:"KIM JAE HWAN"},
                {id:4,cont:"露出or遮盖"},
                {id:5,cont:"挑战新的运动"}
            ],
            imgs:[banner1,banner2,banner3,banner4,banner5],
            index:0,
            onoff:1,
            timer:null
        }
    }
    addEvent = (eve) =>{
        // console.log(eve.target);
        let ali = this.refs.aul.children;
        for(var j=0;j<ali.length;j++){
            ali[j].style.background="";
        }
        
        eve.target.style.background="rgba(237,50,71,.8)"
        
    };
    leftEvent = () =>{
        if(this.state.index === 0){
            this.state.index=this.refs.aimg.children.length-1;
        }else{
            this.state.index--
            // console.log(2)
        }
        // console.log(this.state.index)
        this.move(this.state.index)
    }
    rightEvent = () =>{
        if(this.state.index === 4){
            this.state.index=0;
        }else{
            this.state.index++
            // console.log(2)
        }
        console.log(this.state.index)
        this.move(this.state.index)
    }
    move = (index) =>{
        // console.log(this.refs.aimg)
        let aimg = this.refs.aimg.children
        let ali = this.refs.aul.children;
        let obanner = this.refs.banner12
        for(let i=0;i<aimg.length;i++){
            for(var j=0;j<aimg.length;j++){
                aimg[j].style.display="none";
                ali[j].style.background=""
            }
            aimg[index].style.display="block";
            ali[index].style.background="rgba(237,50,71,.8)"
        }
        switch(index){
           
            case 0: 
                obanner.style.background="#A2C164";
                break;
            case 1: 
                obanner.style.background="#E37FB1";
                break;
            case 2: 
                obanner.style.background="#5BB5B6";
                break;
                
            case 3: 
                obanner.style.background="#F8C1C7";
                break;
            case 4: 
                obanner.style.background="#5BB5B6";
            break;
        }
    }
    autoEvent=()=>{
        this.state.onoff=1
        let aimg = this.refs.aimg.children
        this.state.timer = setInterval(()=>{
            if(this.state.index==aimg.length-1){
                this.state.index=0
            }else{
                this.state.index++
            }
            this.move(this.state.index)
        },3000)
    }
    stop=(eve)=>{
        let aimg = this.refs.aimg.children
        if(this.state.onoff==1){
            eve.target.style.backgroundPosition="-60px -37px";
            clearInterval(this.state.timer)      
            this.state.onoff=0
        }else{
            eve.target.style.backgroundPosition="-60px 0";
            this.state.timer = setInterval(()=>{
                if(this.state.index==aimg.length-1){
                    this.state.index=0
                }else{
                    this.state.index++
                }
                this.move(this.state.index)
            },3000)
            this.state.onoff=1
        }
        console.log(this.state.onoff)
    }
    changeColor = () =>{
        let oleft = this.refs.oleft;
        let oright = this.refs.oright;
        oleft.style.backgroundColor="rgba(0,0,0,0.4)"
        oright.style.backgroundColor="rgba(0,0,0,0.4)"
    }
    overColor = () =>{
        let oleft = this.refs.oleft;
        let oright = this.refs.oright;
        oleft.style.backgroundColor=""
        oright.style.backgroundColor=""
    }
    changeColor1 = () =>{
        let oleft = this.refs.oleft1;
        let oright = this.refs.oright1;
        oleft.style.cssText=`background:rgba(0,0,0,0.4);width:60px;height:60px;position:absolute;`
        oright.style.cssText=`background:rgba(0,0,0,0.4);width:60px;height:60px;position:absolute;`
        
    }
    overColor1 = () =>{
        let oleft = this.refs.oleft1;
        let oright = this.refs.oright1;
        oleft.style.backgroundColor="rgba(0,0,0,0.0)" 
        oright.style.backgroundColor="rgba(0,0,0,0.0)" 
        
    }
    componentDidMount=()=>{
        this.refs.aul.children[0].style.background="rgba(237,50,71,.8)"
        this.refs.banner12.style.background="#A2C164"
        this.autoEvent()
    }
    componentWillUnmount=()=>{
        clearInterval(this.state.timer)
    }
    render() {
        return (
        <div id="Swiper">
            <section className={styles.banner12} ref="banner12" onMouseOver={this.changeColor} onMouseOut={this.overColor}>
                <div className={styles.layout}>
                    <div className={styles.banner}>
                        <div className={styles.banner1} ref="aimg">
                            {
                                this.state.imgs.map((item,index)=>{
                                    return <img src={item} 
                                            className={styles.img} 
                                            key={index}
                                            />
                                })
                            }
                        </div>
                        <ul ref="aul">
                            {
                                this.state.data.map((item,index)=>{
                                    return  <li key={item.id}
                                                onClick={this.addEvent}
                                                
                                                >
                                                {item.cont}
                                            </li>
                                })
                            }
                        </ul>
                        <button onClick={this.stop}></button>
                        <div className={styles.left} 
                             onClick={this.leftEvent}
                             onMouseOver={this.changeColor1} 
                             onMouseOut={this.overColor1}
                             ref="oleft"
                             >
                            <p className={styles.xixi} ref="oleft1"></p>
                        </div>
                        
                        <div className={styles.right}
                            onClick={this.rightEvent}
                            onMouseOver={this.changeColor1} 
                            onMouseOut={this.overColor1}
                            ref="oright"
                        >
                            <p className={styles.haha} ref="oright1"></p>
                        </div>
                        
                        <a href="#" className={styles.zb}>
                            <img src={gg}/>
                        </a>
                    </div>
                </div>
            </section>
        </div>
        )
    }
}
