import React, { Component } from 'react';
import Header from './Header';
import Footer from './Footer';

import {Switch,Route,Redirect} from 'react-router-dom';
import Home from '../pages/Home';
import Follow from "../pages/Follow";
import Column from "../pages/Colume";
import Login from '../pages/Login';
import Reg from '../pages/Reg';
import ErrorPage from "../pages/ErrorPage";
import User from "../pages/User";
import Detail from "../pages/Detail";
import Loading from "../components/Loading/loading";

import '../assets/css/base.css';
import '../library/font';
// import PubSub from 'pubsub-js'
import * as types from "../store/types";
import {connect} from "react-redux";

class App extends Component{
    render(){
      let {bLoading, bNav, bFoot} = this.props
      // console.log(bLoading)
      return (
        <div className="App">
           {bLoading && <Loading /> }
          <Header/>
          
          <Switch>
            <Route path="/home" component={Home} />
            <Route path="/follow" component={Follow} />
            <Route path="/column" component={Column} />
            <Route path="/user" component={User} />
            {/* <AuthUser path="/user" component={User} /> */}
            <Route path="/login" component={Login} />
            <Route path="/reg" component={Reg} />
            <Route path="/detail/:id" component={Detail} />
            <Redirect exact from="/" to="/home" />
            <Route component={ErrorPage}/>
          </Switch>
          <Footer/>
        </div>
      )
    }
  }

const initMapStateToProps = state => ({
  bNav:state.bNav,
  bFoot:state.bFoot,
  bLoading:state.bLoading,
});

const initMapDispatchToProps = dispatch => ({
  viewNav: (bl) => dispatch({type:types.VIEW_NAV,payload:bl}),
  viewFoot: (bl) => dispatch({type:types.VIEW_FOOT,payload:bl}),
});

export default connect(
  initMapStateToProps,
  initMapDispatchToProps
)(App)
