import React, { Component } from 'react';
import styles from "../assets/css/login.module.css";
import axios from '../utils/axios';
import {NavLink} from "react-router-dom";
export default class Login extends Component {
  constructor(){
    super()
    this.state = {
      user:'',
      password:'',
    }
    
  }
  login=()=>{

    axios({
      url:'/api/login',
      method:'POST',
      data:{username:this.state.user, password:this.state.password}
    }).then(
      res => {
        // console.log(this.state.user,this.state.password)
        // console.log(res)
        // this.setState({res:res.data})
        if (res.data.mess==="登录成功"){
          localStorage.setItem('user',JSON.stringify(res.data))
          this.props.history.push('/home')
        } else{
          
          alert("登录失败")
        }
      }
    )
  }
  render() {
    return (
      <div id="Login">
        <main className={styles.layout}>
            <h3>欢迎访问 STREET</h3>
            <div className={styles.main_a}>
                <div className={styles.main_l}>
                    <p>ID/密码登录</p>
                    <ul>
                        <li><span>用户ID</span>
                            <input 
                            type="text" 
                            placeholder="11street ID" 
                            className={styles.txt1}
                            value={this.state.user}
                            onChange={(eve)=>{this.setState({user:eve.target.value})}}
                            />
                            
                        </li>
                        <li>
                            <span>密码</span>
                            <input 
                            type="password" 
                            className={styles.PW}
                            value={this.state.password}
                            onChange={(eve)=>{this.setState({password:eve.target.value})}}
                            />
                            <input 
                            type="button" 
                            value="登录" 
                            className={styles.btn0}
                            onClick={this.login}
                            />
                        </li>
                    </ul>
                    <i></i>

                </div>
                <div className={styles.main_r}>
                    <h4>立即注册</h4>
                    <h5>您还未创建 11STREET帐户？</h5>
                    <p>在 11STREET网站上可免费创建帐户。<br/>创建账户后，您可以享受各种各样的客户福利，包括折扣和奖励。</p>
                    <NavLink to="/Reg">
                      <b>创建账户</b>
                    </NavLink>
                    
                </div>
            </div>
        </main>
      </div>
    )
  }
}

