import React, { Component } from 'react'
import Swiper from '../components/Swiper/Swiper';
import styles from '../assets/css/home.module.css'
import img1 from "../assets/images/Hb.jpg";
import img2 from "../assets/images/Yj.jpg";
import img3 from "../assets/images/Jd.jpg";
import img4 from "../assets/images/Ct.jpg";
import img5 from "../assets/images/Facebooklogo.jpg";
import img6 from "../assets/images/Wxlogo.jpg";
import img7 from "../assets/images/Xllogo.jpg";
import img8 from "../assets/images/pay.jpg";
import img9 from "../assets/images/about11Street.jpg";
// import axios from '../utils/axios';
import {Link} from 'react-router-dom';


export default class Home extends Component {
  constructor(){
    super();
    this.state = {
      img:[img1,img2,img3,img4],
      // img2:[img5,img6,img7,img8],
      detail:[],
      detail1:[],
      dataName:"home"
    }
  };
  changeIndex = (i,eve) =>{
    let al=this.refs.al.children
    // console.log(al)
    for(let i = 0;i<al.length;i++){
      al[i].style.backgroundColor=""
      al[i].style.color="#666"
      // console.log(al[i])
    }
   
    eve.target.style.backgroundColor="#666"
    eve.target.style.color="#fff"
    this.addCont(i)
  }
  addCont(i){
    let arr=[]
    for(let j = (i-1)*8;j<(i-1)*8+8;j++){
      arr.push(this.state.detail[j])
    }
    this.setState({detail1:arr})
}
  componentDidMount(){
    axios({
      url:'/api/home',
      params:{_page:1,_limit:24}
    }).then(res=>{
      // console.log(res.data)
      this.setState({detail:res.data.data})
      // console.log(this.state.detail)
      let arr = []
      for(let i = 0;i<8;i++){
        arr.push(this.state.detail[i])
      }
      // console.log(arr)
      this.setState({detail1:arr})
    })
    this.refs.al.firstElementChild.style.backgroundColor="#666"
    this.refs.al.firstElementChild.style.color="#fff"
  }
  render() {
    // console.log(this.state.detail)
    
    return (
      <div id="Home">
        <Swiper/>
        <div className={styles.main}>
          <div className={styles.main_news}>
            <div className={styles.layout}>
                <h3 className={styles.h3}>最新韩流</h3>
                <ul>
                    {
                      this.state.img.map((item,index) => {
                        return (
                              <li key={index}>
                                  <a>
                                      <img src={item}/>
                                  </a>
                              </li>
                        )
                      })
                    }
                </ul>
            </div>
          </div>

          <div className={styles.main_best}>
            <div className={styles.layout}>
            <Link to="/follow"><h3>特惠专区</h3></Link>
              <p className={styles.tjzq} ref = "al">
                  <a onClick={this.changeIndex.bind(null,1)}>推荐</a>
                  <span></span>
                  <a onClick={this.changeIndex.bind(null,2)}>服饰</a>
                  <span></span>
                  <a onClick={this.changeIndex.bind(null,3)}>美妆</a>
              </p>
              <ul>
                  {
                    this.state.detail1.map((item,index)=>{
                      return (
                        <li 
                        key={index}
                         >
                           <Link to={{pathname:'/detail/'+item._id,search:`${this.state.dataName}`}}>
                            <div className={styles.cont_1}>
                                <img src={item.url} />
                                <p>{item.cont}</p>
                                <div>
                                    <span>{item.price}</span>
                                </div>
                            </div>
                            </Link>
                        </li>

                      )
                    })
                  }
              </ul>
            </div>
          </div>
          <div className={styles.main_11s}>
            <div className={styles.layout}>
            <img src={img9} className={styles.logo3}/>
              <div className={styles.a11s_r}>
                  <div className={styles.a11s_r_l}>
                      <div className={styles.shangfang}>
                          <h3>最新公告</h3>
                          <em>更多</em>
                      </div>
                      
                      <i></i>
                      <p><span></span><b>Global 11STREET 会员购买等级制度的更改通知</b></p>
                      <p><span></span><b>春节连休期间配送/客服放假通知</b></p>
                      <p><span></span><b>全球配送加拿大路线暂时中断通知</b></p>
                      <p><span></span><b>BLACKPINK 活动中奖者通知</b></p>
                  </div>
                  <div className={styles.a11s_r_c}>
                      <div className={styles.shangfang}>
                          <h3>常见问题</h3>
                          <em>更多</em>
                      </div>
                      
                      <i></i>
                      <p><span></span><b>预计和实际国际运费是什么</b></p>
                      <p><span></span><b>配送为什么会延迟</b></p>
                      <p><span></span><b>订单在未收到通知的情况下取消了</b></p>
                      <p><span></span><b>国际配送限制事项</b></p>
                  </div>
                  <div className={styles.a11s_r_r}>
                      <div className={styles.shangfang}>
                          <h3>常联系</h3>
                      </div>
                      <i></i>
                      <img src={img5} />
                      <img src={img6} />
                      <img src={img7} />
                  </div>
              </div>
          </div>
          <div className={styles.main_pay}>
              <div className={styles.layout}>
                  <img src={img8} />
              </div>
          </div>
          </div>

        </div>
      </div>
    )
  }
  
}
