import React, { Component } from 'react'
import styles from "../assets/css/detail.module.css";
import axios from '../utils/axios';

export default class Detail extends Component {
    state = {
        detail:{}
    }
    componentDidMount=()=>{
        let {history,match,location} = this.props
        // console.log(1,match.params.id,location.search)
        let id = match.params.id
        // console.log(id)
        let dataName=location.search.slice(1)
        axios({
            url:`/api/${dataName}/${id}`
        }).then(res=>{
            // console.log(res.data.data)
            this.setState({detail:res.data.data})
            // console.log(this.state.detail)
        })
    }
    show =()=>{
        this.refs.span.style.display = "block";
        this.refs.bBox.style.display = "block";
    };
    hide =()=>{
        this.refs.span.style.display = "none";
        this.refs.bBox.style.display = "none";
    };
    move = (e)=>{
        let sBox=this.refs.sBox
        let bBox=this.refs.bBox
        let span=this.refs.span
        let bImg=this.refs.bImg
        var l = (e.pageX - sBox.offsetLeft - span.offsetWidth/2)-230;
                    
        var t = (e.pageY - sBox.offsetTop - span.offsetHeight/2)-270;
        
        if(l<0) l=0;
        if(t<0) t=0;
        if(l > sBox.offsetWidth - span.offsetWidth){
            l = sBox.offsetWidth - span.offsetWidth;
        }
        if(t > sBox.offsetHeight - span.offsetHeight){
            t = sBox.offsetHeight - span.offsetHeight;
        }
        
        span.style.left = l + "px";
        span.style.top = t + "px";

        var x = l/(sBox.offsetWidth - span.offsetWidth);
        var y = t/(sBox.offsetHeight -span.offsetHeight);
        
        bImg.style.left = x * -(bImg.offsetWidth-bBox.offsetWidth) + "px";
        bImg.style.top = y * -(bImg.offsetHeight-bBox.offsetHeight) + "px";
    }
  render() {
    return (
      <div id="Detail">
        <main>
            <div className={styles.xq}>
                <div className={styles.xq_l}>
                    <div 
                     ref="sBox"
                     className={styles.s_box}
                     onMouseOver={this.show}
                     onMouseOut={this.hide}
                     onMouseMove={this.move}
                    >
                        <img src={this.state.detail.url}/>
                        <span ref = "span"></span>
                    </div>
                    <div 
                    ref = "bBox"
                    className={styles.b_box}
                    >
                        <img 
                         ref="bImg"
                         src={this.state.detail.url}/>
                    </div>
                </div>
                <div className={styles.xq_r}>
                    <div className={styles.xq_r_t}>
                        <h3>{this.state.detail.cont}</h3>
                        <p>{this.state.detail.price}</p>
                    </div>
                    <div className={styles.xq_r_b}>
                        <span>收藏</span>
                        <span className={styles.gwc}>加入购物车</span>
                        <span>购买</span>
                    </div>
                </div>
            </div>
        </main>
      </div>
    )
  }
}